<template>
    <div v-if="lan=='TW'">
        <h2>: </h2>
        <h4 id="Account"></h4>
        <p>
            
        </p>
        <b-table striped hover :items="account"></b-table>

        <h4 id="Project"></h4>
        <p>
            
        </p>
        <b-table striped hover :items="project"></b-table>

        <h4 id="ProjectGroup"></h4>
        <p>
            
        </p>
        <b-table striped hover :items="projectGroup"></b-table>

        <h4 id="Channel"></h4>
        <p>
            
        </p>
        <b-table striped hover :items="channel"></b-table>
    </div>
    <div v-else>
        <h2>Document: Core</h2>
        <h4 id="Account">Account</h4>
        <p>
            Store user information
        </p>
        <b-table striped hover :items="account"></b-table>

        <h4 id="Project">Project</h4>
        <p>
            Store project information
        </p>
        <b-table striped hover :items="project"></b-table>

        <h4 id="ProjectGroup">Project Group</h4>
        <p>
            Store project group information
        </p>
        <b-table striped hover :items="projectGroup"></b-table>

        <h4 id="Channel">Channel</h4>
        <p>
            Store channel information
        </p>
        <b-table striped hover :items="channel"></b-table>
    </div>
</template>

<script>
import vue from 'vue'
export default vue.extend({
    name: "CoreAPIDCore",
    props:{
        lan: String
    },
    computed:{
        account(){
            if(this.lan == 'TW'){
                return [
                    {: "level", : "Number", : ""},
                    {: "email", : "String", : ""},
                    {: "username", : "String", : ", "},
                    {: "password", : "String", : " ()"},
                    {: "identitycard", : "String", : ""},
                    {: "firstname", : "String", : ""},
                    {: "lastname", : "String", : ""},
                    {: "passportname", : "String", : ""},
                    {: "phone", : "String", : ""},
                    {: "firstdayofwork", : "Number", : ""},
                    {: "lastdayofwork", : "Number", : ""},
                    {: "class", : "String", : " ( ID)"},
                    {: "organ", : "Array<String>", : " ( ID)"},
                    {: "laborinsurance", : "Number", : ""},
                    {: "healthinsurance", : "Number", : ""},
                    {: "pension", : "Number", : ""},
                    {: "block", : "Boolean", : ""},
                    {: "googlekey", : "String", : "Google Docs  Key"},
                    {: "projects", : "Array<String>", : " ( ID)"},
                    {: "private", : "Array<String>", : " ( ID)"},
                ]
            }else{
                return [
                    {name: "level", type: "Number", description: "Class level"},
                    {name: "email", type: "String", description: "Company contact use email"},
                    {name: "username", type: "String", description: "For login use"},
                    {name: "password", type: "String", description: "Hashed password"},
                    {name: "identitycard", type: "String", description: "ID card number"},
                    {name: "firstname", type: "String", description: "First name"},
                    {name: "lastname", type: "String", description: "Last name"},
                    {name: "passportname", type: "String", description: "Passport name"},
                    {name: "phone", type: "String", description: "Company contact use cellphone number"},
                    {name: "firstdayofwork", type: "Number", description: "Join day"},
                    {name: "lastdayofwork", type: "Number", description: "Leave day"},
                    {name: "class", type: "String", description: "User's position in the company (Class ID)"},
                    {name: "organ", type: "Array<String>", description: "User's organizational chart position in the company (organization ID)"},
                    {name: "laborinsurance", type: "Number", description: "Labor insurance spend"},
                    {name: "healthinsurance", type: "Number", description: "Health insurance spend"},
                    {name: "pension", type: "Number", description: "Pension"},
                    {name: "block", type: "Boolean", description: "Account block state"},
                    {name: "googlekey", type: "String", description: "Google document key"},
                    {name: "projects", type: "Array<String>", description: "Join projects list"},
                    {name: "private", type: "Array<String>", description: "Private channel sorted list"},
                ]
            }
        },
        project(){
            if(this.lan == 'TW'){
                return [
                    {: "name", : "String", : ""},
                    {: "secondary", : "String", : ""},
                    {: "belong", : "String", : " ( ID)"},
                    {: "description", : "String", : ""},
                    {: "budget", : "Number", : ", "},
                    {: "startday", : "Number", : ""},
                    {: "endday", : "Number", : ""},
                    {: "sign", : "Number", : ""},
                    {: "initial", : "String", : ""},
                    {: "owner", : "String", : " ( ID)"},
                    {: "group", : "Array<IProjectElement>", : ""},
                    {: "staff", : "Array<IProjectElement>", : ""},
                    {: "role", : "Array<String>", : " ( ID)"},
                    {: "code", : "Array<String>", : " ( ID)"},
                    {: "channel", : "Any", : ""},
                ]
            }else{
                return [
                    {name: "name", type: "String", description: "Display name"},
                    {name: "secondary", type: "String", description: "Secondary name"},
                    {name: "belong", type: "String", description: "Belong which project group (Project Group ID)"},
                    {name: "description", type: "String", description: "Project description"},
                    {name: "budget", type: "Number", description: "Will need this data when calculate spending "},
                    {name: "startday", type: "Number", description: "Project start day"},
                    {name: "endday", type: "Number", description: "Project end day"},
                    {name: "sign", type: "Number", description: "Project sign day"},
                    {name: "initial", type: "String", description: "Initial budget"},
                    {name: "owner", type: "String", description: "Who own the project (Account ID)"},
                    {name: "group", type: "Array<IProjectElement>", description: "Department list"},
                    {name: "staff", type: "Array<IProjectElement>", description: "Staff list"},
                    {name: "role", type: "Array<String>", description: "Role list (Role ID)"},
                    {name: "code", type: "Array<String>", description: "Invite Code list (Invite Code ID)"},
                    {name: "channel", type: "Any", description: "Channel list"},
                ]
            }
        },
        projectGroup(){
            if(this.lan == 'TW'){
                return [
                    {: "name", : "String", : ""},
                    {: "projects", : "Array<String>", : " ( ID)"},
                ]
            }else{
                return [
                    {name: "name", type: "String", description: "Display name"},
                    {name: "projects", type: "Array<String>", description: "Project list (Project ID)"},
                ]
            }
        },
        channel(){
            if(this.lan == 'TW'){
                return [
                    {: "type", : "Number", : ""},
                    {: "groupid", : "String", : " ( ID)"},
                    {: "textSetting", : "TextSetting", : ""},
                    {: "fileSetting", : "FileSetting", : ""},
                    {: "issueSetting", : "IssueSetting", : ""},
                    {: "requestSetting", : "RequestSetting", : ""},
                    {: "billSetting", : "BillSetting", : ""},
                    {: "graphSetting", : "GraphSetting", : ""},
                    {: "outsourceSetting", : "OutsourceSetting", : ""},
                    {: "taskSetting", : "TaskSetting", : ""},
                    {: "PMChannel", : "PMChannel", : ""},
                ]
            }else{
                return [
                    {name: "type", type: "Number", description: "type"},
                    {name: "groupid", type: "String", description: " ( ID)"},
                    {name: "textSetting", type: "TextSetting", description: ""},
                    {name: "fileSetting", type: "FileSetting", description: ""},
                    {name: "issueSetting", type: "IssueSetting", description: ""},
                    {name: "requestSetting", type: "RequestSetting", description: ""},
                    {name: "billSetting", type: "BillSetting", description: ""},
                    {name: "graphSetting", type: "GraphSetting", description: ""},
                    {name: "outsourceSetting", type: "OutsourceSetting", description: ""},
                    {name: "taskSetting", type: "TaskSetting", description: ""},
                    {name: "PMChannel", type: "PMChannel", description: ""},
                ]
            }
        }
    }
})
</script>